@font-face {
  font-family: "InterTight";
  src: url(../fonts/InterTight-Regular.woff) format("woff supports variations"), url(../fonts/InterTight-Regular.woff2) format("woff2 supports variations");
  font-weight: 400 600;
  font-display: swap;
}
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

main {
  display: block;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
  display: block;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

.compensate-for-scrollbar {
  padding-right: 0 !important;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  text-decoration: none;
  color: inherit;
  transition: color 0.2s ease, background-color 0.2s ease, border-color 0.2s ease;
  cursor: pointer;
  background: none;
  font-weight: inherit;
  font-size: inherit;
}

select {
  background-color: inherit;
}

blockquote {
  margin: 0;
}

button {
  border: none;
  font-family: inherit;
  margin: 0;
  padding: 0;
  font-size: inherit;
  border: none;
}

textarea {
  outline: none;
  border: none;
  resize: none;
}

textarea:focus {
  outline-width: 0;
}

button,
input,
textarea,
a {
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

input,
input::placeholder {
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  border: none;
  outline: none;
}

input::placeholder {
  color: inherit;
}

picture {
  display: block;
}
picture img {
  width: 100%;
  max-width: 100%;
}

ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
  margin-block-end: 0;
  margin-block-start: 0;
}

ul li {
  list-style-type: none;
}

ol {
  margin: 0;
  padding: 0;
}

ol li {
  list-style-position: inside;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
}

*, *::after, *::before {
  padding: 0;
  margin: 0;
}

.header {
  background-color: #fff;
  border-radius: 30px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.header__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header__picture img {
  max-width: 35px;
  object-fit: contain;
  aspect-ratio: 1/1;
  object-position: center;
}
.header__menu__list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "InterTight";
}

body {
  min-inline-size: 320px;
  margin: 0 auto;
  font-family: sans-serif;
  letter-spacing: 0;
  font-size: 20px;
  font-weight: 400;
  background-color: #e5edf7;
  color: #133a66;
}

@supports (font-variation-settings: normal) {
  body {
    font-family: "InterTight", sans-serif;
  }
}